import React, { Component, lazy, Suspense } from "react";

import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Swal from "sweetalert2";
import { getListReturnSupplier } from "../actions/returnSupplier";
import { reset } from "redux-form";

import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import { finishSend, progressSend } from "../actions/LogicAction";
import { AxiosMasterGet, AxiosMasterPost } from "../Axios";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
const FormTerimaSupplier = lazy(() => import("./Component/FormTerimaSupplier"));

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    returnSupplier: state.provinsi.returnSupplier,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class TolakanReturnSupplier extends Component {
  constructor(props) {
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    super(props);
    this.state = {
      supplierList: [],
      DataSupplier: [],
      startDate: formatedDate,
      kodeSupplier: "",
      namaSupplier: "",
      tanggal: formatedDate,
      noBon: "",
      status: "",
      tampungBarang: [],
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadarCetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
          formatter: (cell) => (
            <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
          ),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "ReturnSupplier",
                      rowIndex,
                      this.props.dispatch,
                      getListReturnSupplier()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    AxiosMasterGet("suppliers")
      .then((res) =>
        this.setState({
          supplierList: res.data,
        })
      )
      .catch((err) => alert("ERROR GET DATA"));
    this.props.dispatch(getUUID());
    this.props.dispatch(getListReturnSupplier());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  handleSubmit(data) {
    var supp = data.kodeJenis
      ? data.kodeJenis
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var simpan = {
      noFaktur: this.props.noFaktur, //
      nettoDiscGlobalExt: 0,
      gross: parseFloat(data.gross), //berat + bungkus
      netto: parseFloat(data.netto), //murni
      qty: 0,
      k: parseFloat(data.k),
      rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
      kadarCetak: parseFloat(data.kadar),
      keterangan: "-",
      discGlobalPr: 0,
      kodeKategori: "-",
      kadarTransaksi: parseFloat(data.kadarTransaksi), //harga%
      kodeJenis: split[0], //kodebarang
      noBarcode: "-",
      p: parseFloat(data.p),
      discGlobalGr: 0,
      pkg: parseFloat(data.pkg),
      bruto: parseFloat(data.bruto), //berat
      nettoDiscGlobal: 0,
    };

    var array = JSON.parse(localStorage.getItem("ReturnSupplier")) || [];
    array.push(simpan);
    localStorage.setItem("ReturnSupplier", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListReturnSupplier());
    this.props.dispatch(reset("TerimaSupplier"));
    document.getElementById("dismissModal").click();
  }

  returnSupplierSubmit() {
    if (this.state.noBon === "") {
      warning("Mohon isi nomor bon");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi Tanggal");
      return false;
    } else if (localStorage.getItem("ReturnSupplier") === null) {
      warning("Barang masih kosong, silahkan tambahkan barang");
      return false;
    } else if (this.state.kodeSupplier === "") {
      warning("Mohon pilih Supplier");
      return false;
    }
    let isi = {
      noFaktur: this.props.noFaktur,
      tanggal: this.state.tanggal,
      noBon: this.state.noBon,
      kodeSupplier: this.state.kodeSupplier,
      namaSupplier: this.state.namaSupplier,
      tanggalBarang: "-",
      barangDetails: JSON.parse(localStorage.getItem("ReturnSupplier")),
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(isi));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("ReturnSupplier"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kodeJenis,
        ticket.kadarCetak,
        ticket.p,
        ticket.k,
        ticket.pkg,
        ticket.bruto,
        ticket.kadarTransaksi,
        parseFloat(ticket.netto).toFixed(3),
        "Rp. " + parseFloat(ticket.rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      "",
      "",
      table.map((list) => list.pkg).reduce((a, b) => a + b, 0),
      table.map((list) => list.bruto).reduce((a, b) => a + b, 0),
      "",
      table.map((list) => list.netto).reduce((a, b) => a + b, 0),
      "Rp. " +
        table
          .map((list) => list.rp)
          .reduce((a, b) => a + b, 0)
          .toLocaleString("id-ID"),
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kode Barang",
      "Kadar Cetak (%)",
      "P",
      "K",
      "Bungkus(Gr)",
      "Berat(Gr)",
      "Harga(Gr)",
      "Murni(Gr)",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("spp/dc/tlkrtr", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "No Bon",
              this.state.noBon.toUpperCase(),
              "Tanggal",
              this.state.tanggal,
              "Supplier",
              this.state.kodeSupplier.toUpperCase(),
              "",
              "",
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Tolakan Return Barang Supplier",
              tableRows
            )
          )
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.clear())
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListReturnSupplier()))
          .then(() => localStorage.setItem("isLogin", true))
          .then(() =>
            this.setState({
              noBon: "",
              kodeSupplier: "",
            })
          )
          .then(() => document.getElementById("myForm").reset())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Ditambahkan",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Tolakan Return Supplier" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    <Suspense
                      fallback={<Skeleton width={"100%"} height={400} />}
                    >
                      <FormTerimaSupplier
                        from="tolakan"
                        onSubmit={(data) => this.handleSubmit(data)}
                        noFaktur={this.props.noFaktur}
                      />
                    </Suspense>
                  }
                  title="Tambah Data Barag"
                />
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-12">
                      <form id="myForm">
                        <div className="row">
                          <div className="col-lg-4 col-md-4">
                            <label htmlFor="">Supplier</label>
                            <select
                              name="supplier"
                              id="supplier"
                              className="form-control"
                              defaultValue="DEFAULT | DEFAULT"
                              onChange={this.pilihSupplier("kodeSupplier")}
                            >
                              <option value="DEFUALT" key="DEFUALT">
                                SILAHKAN PILIH
                              </option>
                              {this.state.supplierList === [] ? (
                                <option>DATA KOSONG</option>
                              ) : (
                                this.state.supplierList.map((list) => (
                                  <option
                                    value={
                                      list.kodeSupplier +
                                      "|" +
                                      list.namaSupplier
                                    }
                                  >
                                    {list.namaSupplier}
                                  </option>
                                ))
                              )}
                            </select>
                          </div>
                          <div className="col-md-4 col-lg-4">
                            <label htmlFor="">Tanggal</label>
                            <input
                              type="date"
                              id="tanggal"
                              name="tanggal"
                              className="form-control"
                              defaultValue={this.state.startDate}
                              onChange={this.inputChange("tanggal")}
                            />
                          </div>
                          <div className="col-md-4 col-lg-4 ">
                            <label htmlFor="">No Bon</label>
                            <input
                              type="text"
                              id="bon_return"
                              name="bon_return"
                              className="form-control"
                              onChange={this.inputChange("noBon")}
                              value={this.state.noBon}
                            />
                          </div>
                        </div>
                      </form>
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3">
                      <div className="row">
                        <div className="col-lg-6"></div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.props.dispatch(showModal())}
                            >
                              Tambah Data
                              <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      {this.props.returnSupplier ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.returnSupplier}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <ToolkitProvider
                          keyField="id"
                          data={[]}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                                noDataIndication="Tidak Ada Data"
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      )}
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          disabled={this.props.onSend}
                          onClick={() => this.returnSupplierSubmit()}
                        >
                          {this.props.onSend ? (
                            <span
                              className="spinner-border spinner-border-sm mr-3"
                              aria-hidden="true"
                            ></span>
                          ) : null}
                          Simpan
                          <i
                            class="fa fa-paper-plane ml-3"
                            aria-hidden="true"
                          ></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TolakanReturnSupplier);
